@use'base/variables' as v;
@use'base/mixins' as m;

.sobreMi {
    &__contenido {
        @include m.tablet {
            display: grid;
            grid-template-columns: 30% 70%;
            gap: 1.2rem;
        }        
    
        &--imagen {
            picture {
                source,img {
                    border-radius: 2rem;
                    max-width:100%;
                }
            }
        }
    
        p {
            @include m.contenedor-md;
            @include m.texto-fluido(1rem, 2rem);
            color: v.$blanco;
            font-weight: v.$delgada;
            text-align: justify;
            line-height: 1.5;
            span {
                color: v.$verde;
                font-weight: v.$bold;
            }
        }
    
        &--lista {
            @include m.contenedor-md;
            ul {
                @include m.tags(v.$principal-claro, 1rem, v.$verde-claro);
                text-transform: uppercase;
                @include m.grid(3, 1rem);
            }
        }
    }
}